<template>
  <div>
    <section class="container my-4 text-center" v-if="cartLength > 0">
      <h1 class="text-secondary"></h1>
      <div class="form-row mt-4">
        <div class="col-sm col-12">
          <div
            class="alert alert-primary text-center text-primary alert-rounded"
            role="alert"
          >1. </div>
        </div>
        <div class="col-sm col-12">
          <div class="alert alert-light text-center alert-rounded" role="alert">2. </div>
        </div>
        <div class="col-sm col-12">
          <div class="alert alert-light text-center alert-rounded" role="alert">3. </div>
        </div>
      </div>
    </section>
    <section class="container my-5" v-if="cartLength > 0">
      <div class="row justify-content-center mt-5">
        <div class="col-md-8 col-12">
          <div class="card">
            <div class="card-header d-flex" id="headingOne">
              <button
                class="btn btn-link mr-auto"
                type="button"
                data-toggle="collapse"
                data-target="#collapseOne"
                @click="collapse = !collapse"
              >
                <span v-if="collapse"></span>
                <span v-if="!collapse"></span>
              </button>
              <div v-if="collapse">
                <span class="h3" v-if="cart.total === cart.final_total">${{cart.total}}</span>
                <span class="h3" v-if="cart.total !== cart.final_total">${{cart.final_total}}</span>
              </div>
            </div>
          </div>
          <div id="collapseOne" class="collapse pt-4">
            <table class="table mb-0">
              <thead>
                <th></th>
                <th></th>
                <th></th>
                <th class="text-right"></th>
              </thead>
              <tbody>
                <tr v-for="item in cart.carts" :key="item.id">
                  <td>
                    <button
                      type="button"
                      class="btn btn-outline-danger btn-sm"
                      @click.prevent="removeCartItem(item.id)"
                      :disabled="status.loadingItem === item.id"
                    >
                      <i class="far fa-trash-alt" v-if="status.loadingItem !== item.id"></i>
                      <i class="fas fa-spinner fa-pulse" v-if="status.loadingItem == item.id"></i>
                    </button>
                  </td>
                  <td>
                    {{item.product.title}}
                    <div class="text-primary" v-if="item.coupon">
                      <small></small>
                    </div>
                  </td>
                  <td>{{item.qty}}{{item.product.unit}}</td>
                  <td class="text-right">${{item.total}}</td>
                </tr>
                <tr>
                  <td
                    colspan="4"
                    class="text-right pt-3"
                    v-if="cart.total == cart.final_total"
                    :class="{'text-primary': cart.total == cart.final_total,
                     'h5': cart.total == cart.final_total}">
                     &nbsp;${{cart.total}}
                  </td>
                  <td colspan="4" class="text-right pt-3" v-if="cart.total !== cart.final_total">
                    <del>&nbsp;${{cart.total}}</del>
                  </td>
                </tr>
                <tr v-if="cart.total !== cart.final_total">
                  <td
                    colspan="4"
                    class="text-right text-primary h5 border-top-0"
                  >&nbsp;&nbsp;&nbsp;${{ cart.final_total }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="input-group mt-4 input-group-sm">
            <input type="text" class="form-control" placeholder="" v-model="coupon_code">
            <div class="input-group-append">
              <button class="btn btn-primary" type="button" @click="addCouponCode"></button>
            </div>
          </div>
          <div class="mt-5 text-center">
            <h5 class="mb-3"></h5>
            <form class="text-left" @submit.prevent="createOrder">
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="username">* </label>
                  <input
                    type="text"
                    v-model="form.user.name"
                    class="form-control"
                    id="username"
                    name="username"
                    placeholder=""
                    v-validate="'required'"
                    :class="{'is-invalid': errors.has('username')}"
                  >
                  <span class="text-danger" v-if="errors.has('username')"></span>
                </div>
                <div class="form-group col-md-6">
                  <label for="tel">* </label>
                  <input
                    type="tel"
                    v-model="form.user.tel"
                    class="form-control"
                    id="tel"
                    name="tel"
                    placeholder=""
                    v-validate="'required'"
                    :class="{'is-invalid': errors.has('tel')}"
                  >
                  <span class="text-danger" v-if="errors.has('tel')"></span>
                </div>
              </div>
              <div class="form-group">
                <label for="useremail">* Email</label>
                <input
                  type="email"
                  v-model="form.user.email"
                  class="form-control"
                  name="email"
                  id="useremail"
                  placeholder=" Email"
                  v-validate="'required|email'"
                  :class="{'is-invalid': errors.has('email')}"
                >
                <span class="text-danger" v-if="errors.has('email')">
                  {{ errors.first('email') }}
                </span>
              </div>
              <div class="form-group">
                <label for="address">* </label>
                <input
                  type="text"
                  v-model="form.user.address"
                  class="form-control"
                  name="address"
                  id="address"
                  placeholder=""
                  v-validate="'required'"
                  :class="{'is-invalid': errors.has('address')}"
                >
                <span class="text-danger" v-if="errors.has('address')"></span>
              </div>
              <div class="form-group">
                <label for="comment"></label>
                <textarea
                  name="comment"
                  id="comment"
                  v-model="form.message"
                  class="form-control"
                  cols="30"
                  rows="10"
                  placeholder=""
                ></textarea>
              </div>
              <div class="d-flex justify-content-end">
                <router-link to="/" href="#" class="btn btn-secondary mr-2 btn-shadow">
                  
                </router-link>
                <button class="btn btn-primary mr-2 btn-shadow"></button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
    <section
      class="container my-5 d-flex justify-content-center align-items-center main"
      v-if="cartLength === 0"
    >
      <p>
        <span class="h1 text-secondary">&nbsp;&nbsp;</span>
        <router-link to="/">
          &nbsp;
          <i class="fas fa-angle-right"></i>
        </router-link>
      </p>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      status: {
        loadingItem: '',
      },
      collapse: true,
      coupon_code: '',
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  methods: {
    getCart() {
      this.$store.dispatch('updateLoading', true);
      this.$store.dispatch('getCarts');
      this.$store.dispatch('updateLoading', false);
    },
    removeCartItem(id) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.status.loadingItem = id;
      this.$http.delete(api).then(() => {
        vm.$store.dispatch('getCarts');
        vm.status.loadingItem = '';
        vm.getCart();
      });
    },
    addCouponCode() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.post(api, { data: { code: vm.coupon_code } }).then((response) => {
        vm.$store.dispatch('updateLoading', false);
        console.log(response.data);
        if (response.data.success) {
          vm.$store.dispatch('getCarts');
          vm.coupon_code = '';
          const { message } = response.data;
          const status = 'danger';
          vm.$store.dispatch('messagePush', { message, status });
          this.$store.dispatch('getCarts');
        } else {
          const { message } = response.data;
          const status = 'danger';
          vm.$store.dispatch('messagePush', { message, status });
        }
      });
    },
    createOrder() {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      const vm = this;
      const order = vm.form;
      this.$validator.validate().then((valid) => {
        if (valid) {
          this.$http.post(api, { data: order }).then((response) => {
            if (response.data.success) {
              vm.$store.dispatch('getCarts');
              vm.$router.push(`checkout/${response.data.orderId}`);
            }
          });
        } else {
          console.log('');
        }
      });
    },
  },
  computed: {
    ...mapGetters(['cart', 'cartLength']),
  },
  created() {
    this.getCart();
  },
};
</script>

<style>
@media (max-width: 569px) {
  .table {
    font-size: 0.875rem;
  }
}
.main {
  min-height: calc(100vh - (40px + 75px + 84px + 96px));
}
</style>
